.App {
  text-align: center;
  font-family: Montserrat, sans-serif;
}

body {
  margin: 0;
  height: auto;
  width: auto;
  overflow: auto;
  align-items: center;
}

a {
  color: #3E96DD; 
  text-decoration: none;
}

.header {
  background: url('./assets/mount2.jpg');
  background-size: cover;
  box-shadow:inset 0 0 0 2000px rgba(96, 153, 218, 0.3);
  background-repeat: no-repeat;
  width: 100vw;
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.links {
  padding-top: 70;
  padding: 100;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  width: 20vw;
}

.links a i:hover {
  color: #5F8FBB;
  border: 1px solid color(var(--white) a(1));
  border-radius: 50%;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  transition: all 0.3s ease;
  margin: 15 20px;
}

.link {
  font-size: 50px;
  color: #fff;
}

@media screen and (max-width: 820px) {
  .link {
    font-size: 30px;
    color: #fff;
    margin: 2px;
  }
}

.title {
  text-align: center;
  border: 3px solid #3E96DD;
  font-size: 26px;
  font-weight: 500;
  padding: 8px 16px;
  margin-bottom: 10px;
  display: inline-block;
}

.row {
  display: flex;
  flex-direction: row;
  width: 820px;
  margin: auto;
  flex-wrap: wrap;
  justify-content: space-evenly;
  align-content: space-around;
}

@media screen and (max-width: 820px) {
  .row {
    width: 80%;
    margin: auto;
  }
}

.img-with-text {
  width: 320px;
}

.img-with-text img {
  display: block;
  margin: 0 auto;
}

.img-with-text p {
  position: relative;
  left: 20px;
  text-align: left;
}

.about .image-wrapper {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-evenly;
  align-content: space-around;
  margin: 40px auto
}

@media screen and (max-width: 820px) {
  .about .image-wrapper {
    display: None
  }  
}

.about .image-container {
  width: 320px;
  height: 220px;
  position: relative;
  cursor: pointer;
  box-shadow: 1px 2px 4px rgba(0, 0, 0, .8);
  margin: 10px 0;
}

.image-container img {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
}

.image-caption {
  width:100%;
  height:23%;
  background:rgba(0, 0, 0, 0.3);
  position:absolute; 
  opacity:0;
  bottom: 0; 
  left: 0;
  transition:all 300ms ease-in-out;
  -webkit-transition:all 300ms ease-in-out;
  -moz-transition:all 300ms ease-in-out;
  -o-transition:all 300ms ease-in-out;
  -ms-transition:all 300ms ease-in-out;
}

.image-caption h1{
  text-align:center;
  margin:10px;
  text-transform:uppercase;
  font-family: Oxygen,Open Sans,sans-serif;
  font-size:10px;
  color: white;
}

.image-container:hover .image-caption{
  opacity:1;
}

.about {
  text-align: center;
  padding: 40px;
}

.about .me {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: left;
}

.about p {
  line-height: 1.6;
  font-size: 16px;
  margin-bottom: 24px;
  width: 800px;
}

.about a {
  font-size: 20px;
  font-weight: 700;
  transition: all 0.6s ease;
}

.about a:hover {
  font-size: 24px;
}

@media screen and (max-width: 820px) {
  .about p {
    width: 90vw
  }
}

.experience {
  padding: 40px;
  background-color: rgb(212, 206, 206);
  text-align: left;
}

.experience a {
  font-weight: bold;
}

.experience .check {
  position: relative;
  margin: 5px;
  top: -8px;
}
 
.experience li {
  list-style-type: none;
  display: flex;
  flex-direction: row;
  margin: 5px;
}

.projects {
  padding: 40px;
  text-align: center;
}

@media screen and (max-width: 820px) {
  .projects {
    padding: 0px;
    padding-top: 10px;
    text-align: center;
  }

  .img-with-text {
    width: 90%;
  }
}


/* The container <div> - needed to position the dropdown content */
.dropdown {
  z-index: 100;
  position: absolute;
  display: inline-block;
  width: 60px;
  height: 40px;
  top: 50px;
  left: 50px;
}


@media screen and (max-width: 820px) {
  .dropdown {
    z-index: 100;
    position: absolute;
    display: inline-block;
    width: 60px;
    height: 40px;
    top: 10px;
    left: 10px;
  }

  /* Dropdown Button */
  .dropbtn {
    background-color: transparent;
    border-style: solid;
    border-color: white;
    border-radius: 10px;
    position: absolute;
    color: white;
    font-size: large;
    cursor: pointer;
    outline: none;
  }

  .dropdown-content {
    text-align: left;
    font-size: small;
    display: block;
    z-index: 100; 
    transition: all 0.5s linear;
    position: relative;
    top: 70px; left: 20px;
  }  

  .dropdown-content .nav-button {
    font-weight: bold;
    font-size: small;
    color: white;
    margin: 10px;
    transition: 0.3s;
    cursor: pointer;
  }
}


/* Dropdown Button */
.dropbtn {
  background-color: transparent;
  border-style: solid;
  border-color: white;
  border-radius: 10px;
  position: absolute;
  color: white;
  font-size: xx-large;
  cursor: pointer;
  outline: none;
}

/* Dropdown button on hover & focus */
.dropbtn:hover, .dropbtn:focus {
  background-color: white;
  color:  rgb(124, 157, 226);
  transition: ease-in-out 1s;
}

.dropdown-content {
  text-align: left;
  display: block;
  z-index: 100; 
  transition: all 0.5s linear;
  position: relative;
  top: 70px; left: 20px;
}

.dropdown-content .nav-button {
  font-weight: bold;
  font-size: large;
  color: white;
  margin: 10px;
  transition: 0.3s;
  cursor: pointer;
  margin-bottom: 10px;
}

.dropdown-content .nav-button:hover {
  color: rgb(124, 157, 226);
}

.interests {
  padding: 40px;
  background-color: rgb(212, 206, 206);
}

.interest-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  flex-wrap: wrap;
}

.interest-box {
  display: flex;
  flex-direction: row;
  margin: 20px;
  margin-left: 100px;
  margin-right: 100px;
  border-radius: 10px;
  font-size: xx-large;
}

.interest-box i {
  margin-top: 15px;
}

.interest-box .icon {
  display: flex;
  flex-direction: column;
  max-width: 50px;
}

.interest-box ul {
  list-style-type:none;
}

.interest-box li {
  margin: 10px;
}

.interest-box .icon div {
  font-size: large;
}

.interest-box a {
  text-decoration: none;
  font-weight: bold;
}

.interest-box .text {
  margin-left: 20px;
  text-align: left;
  font-size: initial;
}

@media screen and (max-width: 820px) {
  .interest-box {
    display: flex;
    flex-direction: row;
    margin-left: 1px;
    margin-right: 1px;
    margin: 0;
    border-radius: 10px;
    font-size: large;
  }

  .interest-box .icon {
    position: relative;
    top: 15px;
  }

  .interest-box .icon div {
    font-size: small;
  }

  .interest-box ul {
    padding: 3px;
  }

  .interest-box .text {
    margin-left: 0px;
  }
}

.project-list {
  height: 100vh;
  overflow: auto;
  padding-bottom: 20px;
  background-color: rgb(212, 206, 206);
}

.project-content {
  padding-top: 15vh;
  margin: auto;
  width: 80%;
}

.project-title {
  font-size: xx-large;
  font-weight: bold;
  margin-bottom: 50px;
}

.project-item {
  padding: 30px;
  display: flex;
  flex-direction: row;
  justify-content: left; 
}

.project-text {
  position: relative;
  text-align: left;
  top: -10px;
}

.project-img {
  width: 150px;
  height: 150px;
  margin-right: 50px;
}

.project-image {
  border-radius: 14px;
  max-width: 80%;
  height: 16rem;
  margin: auto;
  box-shadow: 1px 2px 4px rgba(0, 0, 0, .8)
}

@media screen and (max-width: 820px) {
  .project-image {
    border-radius: 14px;
    width: 220px;
    height: 220px;
    box-shadow: 1px 2px 4px rgba(0, 0, 0, .8)
  }
}

@media screen and (max-width: 820px) {
  .project-item {
    padding: 30px;
    display: flex;
    flex-direction: column;
    justify-content: left; 
  }  
}

@media screen and (max-width: 820px) {
  .section {
    padding: 0px;
    padding-top: 10px;  
    padding-bottom: 15px;
  }
}
